import { useState } from 'react';
import {
    AppShell,
    Navbar,
    Header,
    Text,
    MediaQuery,
    Burger,
    useMantineTheme,
    Divider,
} from '@mantine/core';
import Main from './Main';

import { IconUserPlus, IconUser, IconPlus, IconLogout, IconEdit } from '@tabler/icons';
import { useNavigate } from 'react-router-dom';
import AddUser from './AddUser';
import AddGroup from './AddGroup';
import EditGroup from './EditGroup';
// import EditUser from './EditUser';

export default function Appshell() {
    const navigate = useNavigate()
    const theme = useMantineTheme();
    const [opened, setOpened] = useState(false);
    const [selector, setSelector] = useState(1)


    return (
        <AppShell
            styles={{
                main: {
                    background: theme.colorScheme === 'dark' ? theme.colors.dark[8] : theme.colors.gray[0],
                },
            }}
            navbarOffsetBreakpoint="sm"
            asideOffsetBreakpoint="sm"
            navbar={
                <Navbar p="md" hiddenBreakpoint="sm" hidden={!opened} width={{ sm: 200, lg: 300 }}>
                    <div className='nav-wrapper'>
                        {/*  */}
                        <div className='nav-item' onClick={() => {
                            setSelector(1)
                            setOpened(false)
                        }}>
                            <div className='icon-wrapper'>
                                <IconUser size={20} />
                            </div>
                            <Text>Students</Text>
                        </div>
                        <div onClick={() => {
                            setSelector(2)
                            setOpened(false)
                        }} className='nav-item'>
                            <div className='icon-wrapper'>
                                <IconPlus size={20} />
                            </div>
                            <Text>Add group</Text>
                        </div>

                        <div className='nav-item' onClick={() => {
                            setSelector(4)
                            setOpened(false)
                        }}>
                            <div className='icon-wrapper'>
                                <IconEdit size={20} />
                            </div>
                            <Text>Edit Group</Text>
                        </div>

                        <div className='nav-item' onClick={() => {
                            setSelector(3)
                            setOpened(false)
                        }}>
                            <div className='icon-wrapper'>
                                <IconUserPlus size={20} />
                            </div>
                            <Text>Add User</Text>
                        </div>

                        {/* <div className='nav-item'>
                            <div className='icon-wrapper'>
                                <IconUserPlus size={20} />
                            </div>
                            <Text>Add User</Text>
                        </div> */}
                        <Divider />
                        <Text sx={{ padding: "10px" }}>Version 1.0.0</Text>

                        {/*  */}
                    </div>
                </Navbar>
            }


            header={
                <Header height={{ base: 50, md: 50 }} p="xs">
                    <div style={{ display: 'flex', alignItems: 'center', height: '100%' }}>
                        <MediaQuery largerThan="sm" styles={{ display: 'none' }}>
                            <Burger
                                opened={opened}
                                onClick={() => setOpened((o) => !o)}
                                size="sm"
                                color={theme.colors.gray[6]}
                            // mr="xl"
                            />
                        </MediaQuery>
                        <div className='navbar'>
                            <Text>Leap  English</Text>
                            <IconLogout onClick={() => {
                                if (window.confirm("Are u sure ?") == true) {
                                    localStorage.clear()
                                    navigate("/")
                                } else {
                                }
                            }} />
                        </div>
                    </div>
                </Header>
            }
        >

            {selector == 1 &&
                <Main />
            }

            {selector == 2 &&
                <AddGroup />
            }

            {selector == 3 &&
                <AddUser />
            }

            {selector == 4 &&
                <EditGroup />
            }

            {/* {selector == 5 &&
                <EditUser />
            } */}

        </AppShell>
    );
}