import { Button, Checkbox, Input, LoadingOverlay, Modal, Radio, Select, Text } from '@mantine/core';
import React, { useEffect, useState } from 'react'
import toast from 'react-hot-toast';
import { useQuery, useQueryClient } from 'react-query';
import UserMenu from './Menu';
import axios from "axios"

export default function Main() {
    const tempQuery = useQueryClient()
    const dd = ["Full Name", "Email", "GroupId", "", "State"];
    const [forFilter, setForFilter] = useState([])
    const [text, setText] = useState("")
    const [value, setValue] = useState('0');
    const [tbody, setTbody] = useState([])
    const [opened, setOpened] = useState(false);

    const [groups, setGroups] = useState([])
    const [selectedGroup, setSelectedGroup] = useState()
    const [selectedUser, setSelectedUser] = useState(false)



    const { data, isFetching } = useQuery("allUser", () => {
        let token = localStorage.getItem("token")
        return fetch("https://leap-english.lavina.tech/api/v1/users/", {
            headers: {
                "Authorization": "Bearer " + token
            }
        }).then(res => res.json()).then(data => {
            if (data?.isOk) {
                return data
            } else {
                toast.error("error " + data?.message);
            }

        }).catch(err => {
            toast.error("error " + err?.response?.data?.message);
        })

    }, {
        staleTime: 3600 * 1000 * 24,
        cacheTime: 3600 * 1000 * 24,
    })


    const { data: allgroup, isFetching: allGroupFetching } = useQuery("allGroup", () => {
        let token = localStorage.getItem("token")
        return fetch("https://leap-english.lavina.tech/api/v1/groups", {
            headers: {
                "Authorization": "Bearer " + token
            }
        }).then(res => res.json()).then(data => {
            if (data?.isOk) {
                return data
            } else {
                toast.error("error " + data?.message);
            }
        }).catch(err => {
            toast.error("error " + err?.response?.data?.message);
        })
    }, {
        staleTime: 3600 * 1000 * 24,
        cacheTime: 3600 * 1000 * 24,
    })

    const getGroupName = (id) => {

        if (allgroup?.data) {

            let gg = allgroup.data.find(e => e.id === id)

            if (gg === undefined) {
                return id
            } else {
                return gg.name
            }

        } else {
            return id
        }

    }


    useEffect(() => {
        if (data && Array.isArray(data.data)) {
            let tempArr = data.data.filter(e => {
                return e.email !== "leapadmin@gmail.com"
            })
            setForFilter(tempArr)
            setTbody(tempArr)
        }
    }, [data])


    useEffect(() => {
        if (allgroup && Array.isArray(allgroup.data)) {
            let temp = []
            allgroup.data.forEach(element => {
                temp.push({ label: element.name, value: element.id })
            });
            setGroups(temp)
        }
    }, [allgroup])



    const filter = () => {
        if (text.trim() !== "") {
            let tempSearch = forFilter.filter(e => {
                return e.full_name.toLowerCase().includes(text.toLowerCase())
            })
            setTbody(tempSearch)
        } else {
            setTbody(forFilter)
        }
    }

    const editUserGroup = () => {
        setOpened(false)

        let token = localStorage.getItem("token")
        axios.patch("https://leap-english.lavina.tech/api/v1/users/" + selectedUser.id, { "groupId": selectedGroup }, {
            headers: {
                "Authorization": "Bearer " + token
            },
        })
            .then((res) => {
                if (res?.data?.isOk) {
                    toast.success('Successfully');
                    tempQuery.resetQueries("allUser")
                    setSelectedUser(false)
                    setSelectedGroup("")
                } else {
                    toast.error("error " + res?.data?.message);
                    setSelectedUser(false)
                    setSelectedGroup("")
                }
            })
            .catch((err) => {
                toast.error("error " + err?.response?.data?.message);
                setSelectedUser(false)
                setSelectedGroup("")
            });

    }

    useEffect(() => {
        if (forFilter.length > 0) {
            if (value == "1") {
                let tempSearch = forFilter.filter(e => {
                    return e.access == true
                })
                setTbody(tempSearch)
            } else if (value == "2") {
                let tempSearch = forFilter.filter(e => {
                    return e.access == false
                })
                setTbody(tempSearch)
            }
            else if (value == "0") {
                setTbody(forFilter)
            }
        }

    }, [value])


    useEffect(() => {
        if (forFilter.length > 0) {
            filter()
        }
    }, [text])

    // if (isFetching) return <LoadingOverlay visible={true} overlayBlur={5} />


    return (
        <div className='wrapper'>


            <Modal
                centered
                closeOnClickOutside={false}
                closeOnEscape={false}
                opened={opened}
                onClose={() => {
                    setOpened(false)
                    setSelectedUser(false)
                    setSelectedGroup("")
                }}
                title={selectedUser?.full_name}
            >

                <>
                    <Select searchable={false} clearable={false} transition="pop-top-left"
                        transitionDuration={80}
                        transitionTimingFunction="ease"
                        nothingFound="No options" size='md' placeholder='group' data={groups} value={selectedGroup} onChange={(e) => {
                            setSelectedGroup(e)
                        }} />

                    <Button onClick={editUserGroup} fullWidth variant='gradient' mt={20}>Change</Button>
                </>
            </Modal>

            <Text style={{ maxWidth: "1500px", margin: "0 auto" }} variant='gradient' fz="xl" mt={10} mb={10}>O'quvchilar ({tbody?.length})</Text>
            <Input.Wrapper style={{ width: "100%", maxWidth: "1500px", margin: "20px auto 0px auto", display: "flex", justifyContent: "flex-start", alignItems: "center" }} >
                <Input placeholder='qidirish' value={text} onChange={(e) => { setText(e.target.value) }} />
            </Input.Wrapper>

            {/* <div style={{ display: "flex", marginBottom: "10px" }}>

            </div> */}

            <Radio.Group
                value={value}
                onChange={setValue}
                name="favoriteFramework"
                // withAsterisk
                mb={15}
            >
                <Radio value="0" label="all" />
                <Radio value="1" label="actice" />
                <Radio value="2" label="not active" />
            </Radio.Group>

            {/*  */}
            <div className='table-wrapper'>


                <div className='table-header'>


                    {dd.map(e => {
                        return <div key={e} className='header__item'>
                            <Text>{e}</Text>
                        </div>
                    })}

                </div>


                {tbody.map((e) => {
                    return <div key={e.id} className='table-body'>
                        <div className='body-item'>
                            <p className='normalize'>{e.full_name}</p>
                        </div>
                        <div className='body-item'>
                            <p className='normalize'>{e.email}</p>
                        </div>
                        <div onClick={() => {
                            setOpened(true)
                            setSelectedUser(e)
                        }} className='body-item'>
                            <p className='normalize'>{e.groupId ? getGroupName(e.groupId) : "belgilanmagan"}</p>
                        </div>
                        <div className='body-item'>
                            <UserMenu data={e} />
                        </div>
                        <div style={{ backgroundColor: e.access ? "#97DECE" : "#FF8E9E" }} className='body-item'>
                            <p className='normalize'>{e.access ? "Active" : "not Active"}</p>
                        </div>
                    </div>
                })}
            </div>

            {/*  */}
        </div>
    )
}
