import { Button, Divider, Select, Text, TextInput } from '@mantine/core'
import React, { useEffect, useState } from 'react'
import { useQuery, useQueryClient } from 'react-query'
import { IconPlus, IconLink } from '@tabler/icons';
import toast from 'react-hot-toast';
import axios from "axios";





export default function EditGroup() {
    const tempQuery = useQueryClient()
    const [groups, setGroups] = useState([])
    const [selectedGroup, setSelectedGroup] = useState()
    const [formControl, setFormControl] = useState({
        link1: "",
        link2: "",
        link3: "",
        link4: "",
        link5: ""
    })
    const [addMore, setAddMore] = useState(false)
    const { data, isFetching } = useQuery("allGroup", () => {
        let token = localStorage.getItem("token")
        return fetch("https://leap-english.lavina.tech/api/v1/groups", {
            headers: {
                "Authorization": "Bearer " + token
            }
        }).then(res => res.json()).then(data => data)
    }, {
        staleTime: 3600 * 1000 * 24,
        cacheTime: 3600 * 1000 * 24,
    })

    useEffect(() => {
        if (data && Array.isArray(data.data)) {
            const temp = []
            data.data.forEach(element => {
                temp.push({ value: String(element.id), label: element.name })
            });
            setGroups(temp)
        }
    }, [data])

    const change = (e) => {
        setFormControl(prev => ({ ...prev, [e.target.name]: e.target.value.trim() }))
    }

    const changeInfo = () => {
        let link1 = formControl.link1.replace(/\s+/g, '');
        let link2 = formControl.link2.replace(/\s+/g, '');
        let link3 = formControl.link3.replace(/\s+/g, '');
        let link4 = formControl.link4.replace(/\s+/g, '');
        let link5 = formControl.link5.replace(/\s+/g, '');

        let link = `${link1} ${link2} ${link3} ${link4} ${link5}`.trim()

        if (link != "" && selectedGroup !== undefined && selectedGroup !== "") {
            // --------------------------------------

            let token = localStorage.getItem("token")
            axios.patch("https://leap-english.lavina.tech/api/v1/groups/" + selectedGroup, { "link": link }, {
                headers: {
                    "Authorization": "Bearer " + token
                },
            })
                .then((res) => {
                    if (res?.data?.isOk) {
                        setFormControl({
                            link1: "",
                            link2: "",
                            link3: "",
                            link4: "",
                            link5: ""
                        })
                        // tempQuery.resetQueries("allGroup")
                        toast.success('Successfully');
                        setSelectedGroup("")
                    } else {
                        toast.error("error " + res?.data?.message);
                    }
                })
                .catch((err) => {
                    toast.error("error " + err?.response?.data?.message);
                });

            // --------------------------------------

        } else {
            toast.error("malumotlar xato")
        }

    }

    const deleteGroup = () => {
        let token = localStorage.getItem("token")
        axios.delete("https://leap-english.lavina.tech/api/v1/groups/" + selectedGroup, {
            headers: {
                "Authorization": "Bearer " + token
            },
        })
            .then((res) => {
                if (res?.data?.isOk) {
                    setFormControl({
                        link1: "",
                        link2: "",
                        link3: "",
                        link4: "",
                        link5: ""
                    })
                    tempQuery.resetQueries("allGroup")
                    toast.success('Successfully');
                    setSelectedGroup("")
                } else {
                    toast.error("error " + res?.data?.message);
                }
            })
            .catch((err) => {
                toast.error("error " + err?.response?.data?.message);
            });


    }


    const clearLinks = () => {
        let link = ""

        if (selectedGroup !== undefined && selectedGroup !== "") {
            // --------------------------------------

            let token = localStorage.getItem("token")
            axios.patch("https://leap-english.lavina.tech/api/v1/groups/" + selectedGroup, { "link": link }, {
                headers: {
                    "Authorization": "Bearer " + token
                },
            })
                .then((res) => {
                    if (res?.data?.isOk) {
                        toast.success('Successfully');
                        setSelectedGroup("")
                    } else {
                        toast.error("error " + res?.data?.message);
                    }
                })
                .catch((err) => {
                    toast.error("error " + err?.response?.data?.message);
                });

            // --------------------------------------

        } else {
            toast.error("malumotlar xato")
        }



    }


    return (
        <div>
            <Text fz="lg" variant='gradient'>EditGroup ({groups.length})</Text>
            <div className='addUserWrapper'>
                <Select searchable={false} clearable={false} transition="pop-top-left"
                    transitionDuration={80}
                    transitionTimingFunction="ease"
                    nothingFound="No options" size='md' placeholder='group' data={groups} value={selectedGroup} onChange={(e) => {
                        setSelectedGroup(e)
                    }} />

                <Divider my="xs" label="Video links" labelPosition="left" />
                <TextInput onChange={change} name="link1" value={formControl.link1} size='md' placeholder="Video link" icon={<IconLink size={14} />} />

                <Divider my="xs" label="More links" labelPosition="left" />

                {!addMore ? <Button onClick={() => { setAddMore(true) }} mb={20} variant='outline' fullWidth >add more</Button> : <>
                    <TextInput onChange={change} name="link2" value={formControl.link2} size='md' placeholder="Video link" icon={<IconLink size={14} />} />
                    <TextInput onChange={change} name="link3" value={formControl.link3} size='md' placeholder="Video link" icon={<IconLink size={14} />} />
                    <TextInput onChange={change} name="link4" value={formControl.link4} size='md' placeholder="Video link" icon={<IconLink size={14} />} />
                    <TextInput onChange={change} name="link5" value={formControl.link5} size='md' placeholder="Video link" icon={<IconLink size={14} />} />
                    <Button onClick={() => { setAddMore(false) }} mb={20} variant='outline' fullWidth >Hide</Button>
                </>
                }

                <Divider my="xs" label="Apply" labelPosition="left" />

                <Button onClick={() => {
                    changeInfo()
                }} variant='gradient' fullWidth>change</Button>
                <Button onClick={() => {
                    if (selectedGroup !== undefined && selectedGroup !== "") {
                        if (window.confirm("ushbu guruhni o'chirmoqchimisiz ?") == true) {
                            deleteGroup()
                        } else {
                            toast.success("Bekor qilindi")
                        }

                    } else {
                        toast.error("Guruh tanlang")
                    }
                }} mt={20} color="red" variant="outline" fullWidth>Delete this group</Button>

                <Button mt={20} onClick={() => {

                    if (window.confirm("ushbu guruhni linklarini tozalamoqchimisiz?") == true) {
                        clearLinks()
                    } else {
                        toast.success("Bekor qilindi")
                    }

                }} variant='outline' fullWidth>clear links</Button>

            </div>
        </div>
    )
}
