import { Button, Text, TextInput } from '@mantine/core'
import React, { useState } from 'react'
import { IconPlus, IconLink } from '@tabler/icons';
import axios from "axios";
import toast from 'react-hot-toast';
import { useQueryClient } from 'react-query';


export default function AddGroup() {
    const tempQuery = useQueryClient()
    const [group, setGroup] = useState("")
    const [link, setLink] = useState("")

    const addGroup = () => {
        if (group.trim() != "") {
            let token = localStorage.getItem("token")
            const body = JSON.stringify({
                name: group.trim()
            })
            axios
                .post("https://leap-english.lavina.tech/api/v1/groups", body, {
                    headers: {
                        'Content-Type': 'application/json',
                        "Authorization": "Bearer " + token
                    },
                })
                .then((res) => {
                    if (res?.data?.isOk) {
                        toast.success('Successfully created!');
                        setGroup("")
                        setLink("")
                        tempQuery.refetchQueries("allGroup")
                    } else {
                        toast.error("error " + res?.data?.message);
                    }
                })
                .catch((err) => {
                    toast.error("error " + err?.response?.data?.message);
                });
        } else {
            toast.error("Guruh nomini kiriting");
        }
    }
    return (
        <div>
            <Text fz="lg" variant='gradient'>AddGroup</Text>
            <div className='addUserWrapper'>
                <Text mb={10} mt={10} variant='gradient'>Group Name</Text>
                <TextInput onChange={(e) => {
                    setGroup(e.target.value.trim())
                }} name='group' value={group} size='md' placeholder="Group Name" icon={<IconPlus size={14} />} />

                {/* <TextInput onChange={(e) => {
                    setLink(e.target.value.trim())
                }} name='link' value={link} size='md' placeholder="Video link" icon={<IconLink size={14} />} /> */}

                <Button onClick={() => {
                    addGroup()
                }} variant='gradient' fullWidth>Add</Button>
            </div>
        </div>
    )
}
