import { Button } from '@mantine/core'
import React, { useEffect, useState } from 'react'
import { IconUser } from '@tabler/icons';
import axios from "axios";
import { useNavigate } from 'react-router-dom';
import { Loader } from '@mantine/core';
import toast from 'react-hot-toast';

export default function Login() {

    const navigate = useNavigate()

    useEffect(() => {
        let temp = localStorage.getItem("userStatus")
        if (temp === "active") {
            navigate("/main")
        }
    }, [])

    const [form, setForm] = useState({ input: "", password: "" })
    const [makeLoading, setMakeLoading] = useState(false)

    const signinRequest = () => {

        if (form.input.trim() !== "" && form.password.trim() !== "") {

            setMakeLoading(true)
            const body = JSON.stringify({
                email: form.input.trim(),
                password: form.password.trim()
            })

            axios
                .post("https://leap-english.lavina.tech/api/v1/users/signin", body, {
                    headers: {
                        'Content-Type': 'application/json',
                    },
                })
                .then((res) => {
                    setMakeLoading(false)
                    if (res?.data?.isOk) {
                        toast.success("You are welcome")
                        const userData = JSON.stringify(res.data.data.user)
                        localStorage.setItem("userStatus", "active")
                        localStorage.setItem("userInfo", userData)
                        localStorage.setItem("token", res.data.data.accessToken)
                        navigate("/main")
                    } else {
                        toast.error("error " + res?.data?.message);
                    }
                })
                .catch((err) => {
                    setMakeLoading(false)
                    toast.error("error " + err?.response?.data?.message);
                });

        } else {
            toast.error("barcha qismlarni to'ldiring");
        }

    }


    return (
        <div className='login-wrapper'>
            <input id='leap' placeholder='login' value={form.input} onChange={(e) => {
                setForm(prev => ({ ...prev, input: e.target.value }))
            }} />
            <input id='password' type={"password"} placeholder='password' value={form.password} onChange={(e) => {
                setForm(prev => ({ ...prev, password: e.target.value }))
            }} />

            <Button onClick={signinRequest} leftIcon={!makeLoading ? <IconUser size={18} /> : null} w={300} variant="gradient"> {makeLoading ? <Loader size={"sm"} color={"#fff"} /> : "SIgn In"}</Button>
        </div>
    )
}
