import { Button, Divider, Input, LoadingOverlay, Select, Text, TextInput } from '@mantine/core'
import React, { useEffect, useState } from 'react'
import { useQuery, useQueryClient } from 'react-query'
import { IconUser, IconPassword, IconAt } from '@tabler/icons';
import toast from 'react-hot-toast';
import axios from "axios";

export default function AddUser() {
    const tempQuery = useQueryClient()
    const [groups, setGroups] = useState([])
    const [selectedGroup, setSelectedGroup] = useState()
    const [access, setAccess] = useState(true)

    const [formControl, setFormControl] = useState({ name: "", email: "", password: "" })

    const { data: allGroup, isFetching: allGroupFetching } = useQuery("allGroup", () => {
        let token = localStorage.getItem("token")
        return fetch("https://leap-english.lavina.tech/api/v1/groups", {
            headers: {
                "Authorization": "Bearer " + token
            }
        }).then(res => res.json()).then(data => data)
    }, {
        staleTime: 3600 * 1000 * 24,
        cacheTime: 3600 * 1000 * 24,
    })

    useEffect(() => {
        if (allGroup && Array.isArray(allGroup.data)) {
            const temp = []
            allGroup.data.forEach(element => {
                temp.push({ value: String(element.id), label: element.name })
            });
            temp.push({ value: "", label: "belgilanmagan" })
            setGroups(temp)
        }
    }, [allGroup])


    function getRandomInt(max) {
        return Math.floor(Math.random() * max);
    }

    const sendToTelegram = (email) => {
        const telegramUrl =
            "https://api.telegram.org/bot5314711769:AAHXkBGriLHCT5ykWYCe8FINyfspRfxsFcE/";
        const message = "<b>Ism familiyasi: </b> " +
            formControl.name +
            "\n\n" +
            "<b>Email: </b>" + email + "\n\n" + "<b>Password: </b>" + formControl.password
        axios
            .get(telegramUrl + "sendMessage", {
                params: {
                    chat_id: -1001664585665,
                    text: message,
                    parse_mode: "HTML",
                },
            })
    }
    const generate = () => {
        if (formControl.name.trim() != "" && formControl.password != "") {
            let token = localStorage.getItem("token")
            const email = formControl.email.trim() != "" ? formControl.email.trim() : "user" + getRandomInt(99999) + "@mail.com"
            // ---------------------------------------------
            const body = JSON.stringify({
                full_name: formControl.name,
                email: email,
                password: formControl.password.trim(),
                role: "user",
                groupId: selectedGroup ? Number(selectedGroup) : null,
                access: access
            })
            axios
                .post("https://leap-english.lavina.tech/api/v1/users", body, {
                    headers: {
                        'Content-Type': 'application/json',
                        "Authorization": "Bearer " + token
                    },
                })
                .then((res) => {
                    if (res?.data?.isOk) {
                        tempQuery.refetchQueries("allUser")
                        sendToTelegram(res.data.data.email)
                        toast.success('Successfully created!');
                        setFormControl({ name: "", email: "", password: "" })
                        setSelectedGroup(false)
                        setAccess(true)
                    } else {
                        toast.error("error " + res?.data?.message);
                    }
                })
                .catch((err) => {
                    toast.error("error " + err?.response?.data?.message);
                });
        } else {
            toast.error("Barcha qismlarni to'ldiring");
        }

    }

    const change = (e) => {
        setFormControl(prev => ({ ...prev, [e.target.name]: e.target.value }))
    }
    return (
        <>
            <Text fz="lg" variant='gradient'>Add User</Text>
            <div className='addUserWrapper'>
                <TextInput name='name' onChange={change} value={formControl.name} size='md' placeholder="Full Name" icon={<IconUser size={14} />} />
                <TextInput name='password' onChange={change} value={formControl.password} size='md' placeholder="Password" icon={<IconPassword size={14} />} />
                <Select searchable={false} clearable={false} transition="pop-top-left"
                    transitionDuration={80}
                    transitionTimingFunction="ease"
                    nothingFound="No options" size='md' placeholder='group' data={groups} value={selectedGroup} onChange={(e) => {
                        setSelectedGroup(e)
                    }} />
                <Text mb={10} mt={10} variant='gradient'>access</Text>
                <Select searchable={false} clearable={false} transition="pop-top-left"
                    transitionDuration={80}
                    transitionTimingFunction="ease"
                    nothingFound="No options" size='md' placeholder='group' data={[{ value: false, label: 'false' }, { value: true, label: "true" }]} value={access}
                    onChange={(e) => {
                        setAccess(e)
                    }} />
                <Divider />
                <Text mb={10} mt={10} variant='gradient'>Optional email</Text>
                <TextInput onChange={change} name='email' value={formControl.email} size='md' placeholder="Email" icon={<IconAt size={14} />} />

                <Button onClick={generate} variant='gradient' fullWidth>Generate</Button>

            </div>

        </>
    )
}
