import { Menu, Button, Divider } from '@mantine/core';
import { IconTrash, IconUser, IconEdit } from '@tabler/icons';
import axios from "axios";
import toast from 'react-hot-toast';
import { useQueryClient } from 'react-query'
function UserMenu({ data }) {
    const tempQuery = useQueryClient()
    return (
        <>
            <Menu shadow="lg">
                <Menu.Target>
                    <Button style={{ height: "100%" }} size='xs' fullWidth leftIcon={<IconEdit size={18} />} variant='white'>Edit</Button>
                </Menu.Target>

                <Menu.Dropdown sx={{ border: "1px solid #000" }}>
                    <Menu.Item>{data?.full_name}</Menu.Item>

                    <Menu.Item>{data?.email}</Menu.Item>
                    <Divider mt={2} mb={2} />
                    <Menu.Item onClick={() => {
                        let token = localStorage.getItem("token")
                        axios.patch("https://leap-english.lavina.tech/api/v1/users/" + data.id, { "access": data.access ? false : true, }, {
                            headers: {
                                "Authorization": "Bearer " + token
                            },
                        })
                            .then((res) => {
                                if (res?.data?.isOk) {
                                    toast.success('Successfully');
                                    tempQuery.resetQueries("allUser")
                                } else {
                                    toast.error("error " + res?.data?.message);
                                }
                            })
                            .catch((err) => {
                                toast.error("error " + err?.response?.data?.message);
                            });
                        // --------------------------------------------------------
                    }} icon={<IconUser size={14} />}>{data.access ? "Block User" : "Unblock"}</Menu.Item>
                    <Menu.Item onClick={() => {
                        if (window.confirm(`delete ${data?.full_name} ?`) == true) {
                            let token = localStorage.getItem("token")
                            axios.delete("https://leap-english.lavina.tech/api/v1/users/" + data.id, {
                                headers: {
                                    "Authorization": "Bearer " + token
                                },
                            })
                                .then((res) => {
                                    if (res?.data?.isOk) {
                                        toast.success('Successfully');
                                        tempQuery.resetQueries("allUser")
                                    } else {
                                        toast.error("error " + res?.data?.message);
                                    }
                                })
                                .catch((err) => {
                                    toast.error("error " + err?.response?.data?.message);
                                });
                        } else {
                            toast.success('Bekor qilindi');
                        }
                    }} color="red" icon={<IconTrash size={14} />}>delete account</Menu.Item>
                </Menu.Dropdown>
            </Menu>
        </>
    );
}
export default UserMenu